body {
  background-color: #f2f2f2;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  background-color: #fff;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

h1 {
  text-align: center;
}

.is-hidden {
  display: none;
}

input[type="text"] {
  max-width: 100%;
  border: 1px solid #ccc;
  margin-bottom: 10px;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #9932cc;
  border: none;
  margin: auto;
  padding: 10px 20px;
}

button:hover {
  background-color: purple;
}

#imageGallery {
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  display: grid;
}

.imageCard {
  text-align: center;
  border: 1px solid #ccc;
  padding: 10px;
}

.imageCard img {
  max-width: 100%;
  height: auto;
}

.error {
  text-align: center;
}

